@media (min-width: 320px) and (max-width: 480px) {
  #app {
    position: relative;
  }

  #map-container {
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  .popup__container {
    left: 0 !important;
  }

  .interface-container {
    width: 100%;
  }

  .interface-pages-wrap {
    position: absolute;
    top: 0;
    left: 0;
    transition: left 0.3s ease;
  }

  .interface-switcher {
    width: 31px;
    height: 100px;
    background-color: #fff !important;
    border-radius: 0 5px 7px 0;
    position: absolute;
    top: 0;
    left: 287px;
    z-index: 100000;
    box-shadow: 20px 2px 24px rgba(0, 0, 0, 0.06);
    transition: left 0.3s ease;
    display: flex;
  }

  .closed > .interface-switcher {
    left: 0;
    border-radius: 0 5px 30px 0;
    box-shadow: 0 1px 2px 1px rgba(0, 0, 0, 0.15),
      0 2px 5px -3px rgba(0, 0, 0, 0.15);
  }

  .switcher-icon {
    background-size: cover;
    width: 24px;
    height: 24px;
    margin: auto;
    z-index: 100001;
    background-image: url(/static/media/arrow_back.326e03d8.svg);
  }

  .closed .switcher-icon {
    background-image: url(/static/media/arrow_forward.f1f8fec4.svg);
  }

  .closed {
    width: 31px;
    height: 100px;
  }

  .closed > .interface-pages-wrap {
    left: -288px;
  }
}

